

.messenger-chat-box{
    border-radius: 10px;
    padding-bottom: 5px;
  }
  .messenger-chat-height{
    height:420px;
    Overflow-y:scroll;

  }
  .chat-popup-box {
    border-radius: 10px;
    padding-bottom: 10px;
    position: absolute;
    z-index: 1;
    
  }
  .chat-inner-profile-box {
    border-bottom: 1px solid #989a9b;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    background: #f5f5f2;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    align-items: center;
  }
  .popup-Profile-content h3 {
    
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  .popup-Profile-content p {
    
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0em;
    color: #6e7172;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  .chat-entire-input-area-section {
    align-items: center;
  }
  .chatSender{
    display: flex;
    justify-content: flex-end;
    align-items: center; 
  }
  .sender-message-and-time{
    width: fit-content;
    height: fit-content;
    background: #e7fafa;
    border-radius: 10px;
    padding-top: 8px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .chatSender p {
    margin-bottom: -10px;
  }
  .chatSender em{
    font-size: 10px;
  }
  .chat-add-icon {
    background: #489797;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  }
  .chat-add-icon p {
    
  
    font-weight: 700;
    letter-spacing: 0em;
  }
  .extra-options-onclick-onPlus-chat{
    margin-top: -22%;
    position:absolute;
  }
  .extra-options-onclick-onPlus-chat-messenger{
    padding-left: 10px;
    margin-top: -8%;
    position:absolute;
  }
  .replyDiv{
    background-color: rgb(206, 241, 233);
    padding: 1%;
    border-radius: 4px 4px 0px 0px;
    display: flex;
    justify-content: space-between;
  }
  .Chat-height{
    height:300px;
    Overflow-y:scroll;
  }
  .chatSendButton{
    background: white;
    border-style: none;
  }
  .displayImgChat img{
    width: 5rem;
   height: 5rem;
   border-radius: 50%;
  }
  .close-icon{
    display: flex;
    justify-content: flex-end;
  }

  .options-icon-chat{
    color: gray;
    opacity: 20%;
  }
  
  .options-icon-section :hover{
    fill: black;
    opacity: 100%;
    cursor: pointer;
  }

  /* slot book css start here */
  
  .slots-list{
    /* width: 500px; */
    overflow-y: scroll;
    height: 350px;
  }
  .react-calendar{
    border-style: none !important;
    line-height: 1.8em !important;
  }
  .react-calendar__tile:disabled {
    background-color: white !important;
}
.react-calendar__tile--active {
  background: black !important;
  color: #14C6A4 !important;
}