.form-layout {
    display: flex;
    flex-wrap: wrap;
    height: 100vh;
}
.form-layout .form-layout-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #AA96DA;
    box-shadow: 2px 0px 5px 2px rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 100%;
}
.form-layout .form-layout-banner img:nth-child(1) {
    position: absolute;
    top: 30px;
    left: 30px;
}
.logo {
    font-family: Praise !important;
    font-size: 36px !important;
    font-weight: 400 !important;
    line-height: 43px !important;
    letter-spacing: 0em !important;
    color: #489797;
}
.form-layout .form {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.form-layout .form .form-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.form-layout .form .form-input {
    padding: 1rem;
    background-color: #eee;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    margin-bottom: 1rem;
}
.form-layout .form .form-btn {
    width: 100%;
    background-color: #C5FAD5;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.2);
    color: #000;
    padding: 15px;
    border-radius: 10px;
    border: 2px solid #ccc;
    -webkit-border-radius: 10px;
}