

* {
  padding: 0;
  margin: 0;

  
}
.error-textfield .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
  border-color: #e60c0c;
}
.Toastify__toast-theme--colored.Toastify__toast--success {
  background: #056408 !important;
}
.Toastify__toast-container--top-right {
  top: 7em !important;
}

.progress{
  position:relative;
  height:2rem;
  width:2rem;
  border-radius: 50%; 
  display: flex;
  justify-content: center;
  align-items: center;
  float: right;
}
.progress span{
  position:absolute;
  height:1.5rem ;
  width:1.5rem; 
  border-radius: 50%; 
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center; 
  font-size: 0.5rem;
}

.card {
  /* padding: 0.5rem; */
  margin: 0 10px;
  cursor: pointer;
  position: relative;
  display: flex;
}
.hover-text1 {
  position: absolute;
  top:77%;
  left: -38px;
  transform: translate(-50%, -50%);
  visibility: hidden;
  background-color: #000;
  padding: 3px;
  border-radius: 4px;
  white-space: nowrap;
  border: 1px solid #000;
  color:#14C6A4;
  font-size: 12px;
}
.hover-text {
  position: absolute;
  top: 100%;
  left: 30px;
  transform: translate(-50%, -50%);
  visibility: hidden;
  box-shadow:#fff;
  /* background-color: #000; */
  padding: 3px;
  border-radius: 4px;
  white-space: nowrap;
  border: 1px solid #e6e6e6;
  /* color:#14C6A4; */
  font-size: 12px;
}

.card:hover .hover-text {
  visibility: visible;
}

.card:hover .hover-text1 {
  visibility: visible;
}

.chat-container-messenger1{
  height: 392px;
  overflow-y: scroll;
  scrollbar-width: thin; 
  scrollbar-color: transparent transparent; 
}

.chat-container-messenger1::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.chat-container-messenger1::-webkit-scrollbar-track {
  background: transparent;
}

.otpinput{
  width:2.5rem;
  height: 2.5rem;
  text-align: center;
}
.react-tel-input .form-control{
  width:100% !important;
}

