.ProfileStatusTabs-entire-div{
    margin-top: 3%;
}
.Shortlisted{
    padding-top: 6%;
    padding-bottom: 6%;
    padding-left: 3%;
    padding-right: 3%;
    text-align: center;
    margin: 2%;
}
.Shortlisted img{
    padding-bottom: 5%;
    width: 6rem;
    height: 6rem;
}
.no-data{
    display: flex;
    justify-content: center;
    padding-top: 20%;
}
.Shortlisted h2{
    
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    padding-bottom: 3%;
    height: 50px;

}
.Shortlisted h4{
    
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0em;
    padding-top: 6%;
    height: 50px;


}
.Shortlisted h5{
    
    font-size: 14px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    height: 65px;
    

}
.Shortlisted h6{
    
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    margin-bottom: 5%;
    text-overflow: ellipsis ;
    line-clamp: 2;
    height: 3rem;
    text-transform: capitalize;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp :2;
    -webkit-box-orient: vertical

}
.Shortlisted p{
    
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    color: #626567;
    padding-bottom: 5%;
    text-overflow: ellipsis ;
    line-clamp: 2;
    height: 3rem;
    text-transform: capitalize;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp :2;
    -webkit-box-orient: vertical

}
.Shortlisted-button{
    color: white;
    padding: 12px;
    font-weight: 600;
    font-size:14px ;
    border-radius: 10px;
    background: #316539;
    border-style: none;
    text-align: center;
}
.rejected-button{
    color: white;
    font-weight: 600;
    padding: 12px;
    font-size:15px ;
    border-radius: 10px;
    gap: 12px;
    background: #d50707;
    border-style: none;
    width:"215px"
}
.Decision-button{
    display: flex;
    justify-content: center;
}
.Decision-button p{
    
    font-size: 18px;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    color: black;
    padding: 12px;
    border-radius: 10px;
    gap: 12px;
    background: #FFBBBB;

}
.Shortlisted-button p{
    color: white;
    
    font-size: 18px;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;

}
.tab-box{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.ProfileStatusTabs-backbutton button{
    border-style: none;
    background-color: whitesmoke;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 16px;
    padding-right: 16px;
    border-radius: 4px;
}
.ProfileStatusTabs-backbutton{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
    margin-right: 13rem;
}
.ProfileStatusTabs-backbutton button a{
    text-decoration: none;
}
.applicationCourse{
    color:#626567
  }
