.sec-one-main{
    /* background-position: center bottom;
    background-repeat: no-repeat; */
    /* height: 100vh;
    width: 100%;
   */
    background: url("https://res.cloudinary.com/dffdp7skh/image/upload/v1707475454/world_or3w6o.png") no-repeat;
    background-position: center;
    background-size: cover;
    min-height: calc(100vh - 30px);
    position: relative;
    margin-top:3%;
}
.section-one{
    display:flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 5rem;
    padding-top: 5rem;
}
.section-one h1{
    font-weight:700;
    font-size: 52px;
}
.section-one li {
    margin-bottom: 8px;
}

/* section two */
.section-two h1{
    font-weight:700;
    font-size:48px;
    margin-top:6%;
    margin-bottom:6%;
}
.section-two p{
    margin-left: 2rem;
}

/* section three */
.section-three h1{
    font-weight:700;
    font-size:48px;
    margin-top:3%;
    margin-bottom:1%;

}
.section-three p{
    width:60%;
    text-align: center;
}

/* section four */
.section-four{
    display:flex;
    overflow:hidden
}



/* Styles for screens up to 768px */
@media only screen and (max-width: 768px) {
    /* Your CSS rules for smaller screens here */
    .section-one{
        padding-left: 2rem;
        padding-top: 2rem;
    }
    .section-one h1 {
        font-size: 1.5rem;
        /* width: 80%; */
        /* line-height: 38px; */
    }
    .section-one h2 {
        font-size: 1rem;
        /* width: 80%; */
        /* line-height: 38px; */
    }
    .section-one li {
        margin-bottom: 8px;
        width: 273px;
    }
    .section-one p {
        /* width: 85%; */
        font-size: 0.75rem;
        font-weight: 500;
    }
    .sec-one-buttons {
        display: flex;
        font-size: 0.6rem;
        /* flex-direction: column; */
    }
    
    .sec-one-main {
        background: url("https://res.cloudinary.com/dffdp7skh/image/upload/v1707475454/world_or3w6o.png");
        background-position: center;
        background-size: cover;
        min-height: calc(100vh-10px);
        position: relative;
        margin-top: 6%;
        height: auto;
    }
    /* section two */
    .section-two h1{
        font-size:1.4rem;
        text-align: center;
        line-height: 1.75rem;
    }
    .section-two p{
        width:98%;
        font-size: 0.875rem;
    }
    /* section three */
    .section-three h1{
        font-size:1.4rem;
        text-align: center;
        width: 80%;
        line-height: 1.75rem;
    }
    .section-three p{
        width:84%;
        font-size: 0.875rem;
    }
    .section-three-select{
        display: flex;
        flex-direction: column !important;
        align-items: center;
    }
    /* section four */
    .section-four img{
        width: 150px;
    }
    
  }

  @media only screen and (min-width: 769px) and (max-width: 1025px) {
    /* Your CSS rules for medium-sized screens here */
    /* .sec-one-main{
        height: 80vh;
        
    } */
    .section-one h1{
        font-size: 38px;
        line-height: 42px;
        padding-bottom: 12px;
    }
    .section-one p{
        width:85%;
    }
     /* section two */
     .section-two h1{
        font-size:32px;
        text-align: center;
    }
    .section-two p{
        width:78%;
    }
     /* section three */
     .section-three h1{
        font-size:38px;
        text-align: center;
    }
    .section-three p{
        width:84%;
    }
    .section-four div{
        display: flex;
        justify-content: center;
    }
    .section-four img{
        width: 80%;
    }
    
  }
  .profileImg{
      position: relative;
  }
  .progresshover{
    position: absolute;
        visibility: hidden;
    }
  .profileImg:hover .progresshover {
    visibility: visible;
    color:teal;
    font-size: 14px;
  }
  
 