
.skip-div{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 70%;
    padding-bottom: 1%;
}
.login-form{
    display: flex;
    flex-direction: column;
    width: 50%;
}

.signup-Form{
    /* display: flex;
    flex-direction: column; */
    width: 80%;
}
.forgot-form{
    width: 80%;
}
.forgot-continue-btn{
    
    height: 56px;
    width: 386px;
   
}
.verification-form{
    width: 50%;
}
.verification-continue-btn{
    height: 56px;
    width: 386px;
}
.google-icon{
    width: 6%;
}



/* Styles for screens up to 768px */
@media only screen and (max-width: 768px) {
    /* Your CSS rules for smaller screens here */
    .Globe-Image img{
        width: 280px !important;
        padding-top: 2%;
    }
    .skip-div{
        width: 80%;
    }
    .login-form{
        width: 80%;
    }
    .signup-Form{
        width: 80%;
    }
    .forgot-form{
        width: 80%;
    }
    .forgot-continue-btn{
        height: 56px;
        width: 300px;
    }
    .verification-form{
        width: 90%;
    }
    .verification-continue-btn{
        height: 56px;
        width: 300px;
    }
    .google-icon{
        width: 13%;
    }
  }

  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    /* Your CSS rules for medium-sized screens here */
    .Globe-Image img{
        width: 400px !important;
        padding-top: 2%;
    }
    .login-form{
        width: 80%;
    }
    .signup-Form{
        width: 80%;
    }
    .forgot-form{
        width: 80%;
    }
    .forgot-continue-btn{
        height: 56px;
        width: 320px;
    }
    .verification-form{
        width: 90%;
    }
    .verification-continue-btn{
        height: 56px;
        width: 332px;
    }
    .google-icon{
        width: 13%;
    }
  }


