    color: #489797;
  }

  /* css for circular progress bar */

  .circular-progress-bar{
    display: flex;
    justify-content: center;

  }

  .card {
    /* padding: 1rem; */
    margin: 0 10px;
    cursor: pointer;
    position: relative;
    display: flex;
  }
  
  .hover-text {
    position: absolute;
    top: 90%;
    left: 70%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    background-color: #fff;
    padding: 3px;
    border-radius: 4px;
    white-space: nowrap;
    border: 1px solid #000;font-size: 12px;
  }

  .updated {
    position: absolute;
    top: 90%;
    left: 70%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    background-color: #000;
    padding: 3px;
    color:#14C6A4;
    border-radius: 4px;
    white-space: nowrap;
    border: 1px solid #000;font-size: 12px;
  }
  .card:hover .updated {
    visibility: visible;
  }
  
  .card:hover .hover-text {
    visibility: visible;
  }
  .chat-container-messenger{
    height: 72vh;
    overflow-y: scroll;
    scrollbar-width: thin; 
    scrollbar-color: transparent transparent; 
  }
  
  .chat-container-messenger::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .chat-container-messenger::-webkit-scrollbar-track {
    background: transparent;
  }
